import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';

import { ModalController } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
/**
 * Generated class for the CuentaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-cuenta',
  templateUrl: 'cuenta.html',
})
export class CuentaPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController, public alertCtrl: AlertController,public loadingCtrl: LoadingController,public events: Events, public apiProvider: ApiProvider, public storage: Storage) {

  this.editarData=true;
  this.dataUserInput={};
    this.dataUser={};
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CuentaPage');


    this.apiProvider.verificarLogin()
    .then(data => { 

      console.log(data);

      if(data){
      this.dataUserInput = JSON.parse(JSON.stringify(data));;


      this.dataUser = data;
       
      //this.menuActivo = true;
      }
      else{
      console.log('error');
      // this.menuActivo = false;
      } 
      
      
     });


  }

  agregadoOk() {
  let alert = this.alertCtrl.create({
    title: 'Editado correctamente',
    subTitle: 'Sus datos han sido editados correctamente',
    buttons: ['Cerrar']
  });
  alert.present();
}



    getPorcentaje(){

    let enviar = ((900)/(this.dataUser.completadas*100)) + '%';
    console.log(enviar);
    return enviar;
       
  
  }





  guardarCambios(){

         let loading = this.loadingCtrl.create({content : "Cargando ..."});
          loading.present();


          this.apiProvider.editarUsuario(this.dataUser)
          .then(data => {

          loading.dismissAll();

          console.log(data);
          if(data.affectedRows>0){

           //this.storage.set(`usr_tok_by`, this.dataUser);


            this.storage.get('usr_tok_by').then((value) => {
            // console.log(value);
            var da = value;

            da.genero = this.dataUser.genero;
            da.telefono = this.dataUser.telefono;
            da.nombre = this.dataUser.nombre;

            console.log(da);
            this.storage.set(`usr_tok_by`, da);


            }).catch(() => resolve(false))

          this.agregadoOk();
          //console.log('borrada');
          this.editarData=true;

          }
          else{
          console.log('Ha ocurrido un error');
          }


          });




   //console.log(this.dataUser);

  }



  cancelarEdicion(){
   this.dataUser = this.dataUserInput;
   this.editarData=true;

  }

}



